<template>
  <div class="cleared-wrap">
    <div class="cleared-left">
      <left></left>
    </div>
    <div class="cleared-right">
      <div class="cleared-header">
        <div class="left">
          <span class="header-line"></span>
          <span class="header-content">已办理的服务</span>
          <span class="service-num">共3项服务</span>
        </div>
        <div class="search-box">
          <input class="input-search" type="text" placeholder="输入您要搜索的关键词">
          <button class="btn-search" type="button"><i class="iconfont icon-search"></i>&nbsp;&nbsp;搜索</button>
        </div>
      </div>
      <div class="table-box">
        <table>
          <thead>
          <tr>
            <th>序号</th>
            <th>填表名称</th>
            <th>任务名称</th>
            <th>部门</th>
            <th>申请人</th>
            <th>操作</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(item, index) in tableData">
            <td>{{index+1}}</td>
            <td>{{item.tableName}}</td>
            <td>{{item.taskName}}</td>
            <td>{{item.departmentName}}</td>
            <td>{{item.applicant}}</td>
            <td style="width: 300px">
              <span class="check">
                <a class="check-text" href="#"><i class="iconfont icon-check"></i>&nbsp;&nbsp;查看</a>
              </span>
              <span class="flow">
                <a class="flow-text" href="#"><i class="iconfont icon-flow"></i>&nbsp;&nbsp;流程</a>
              </span>
            </td>
          </tr>
          </tbody>
        </table>
        <div class="pages">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="1000">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  import left from './left'
  export default {
    name: 'cleared',
    data() {
      return {
        //表格数据
        tableData: [
          {tableName: '校园卡申请', taskName: '校园卡申请', departmentName: '网络信息中心', applicant: '孙磊'},
          {tableName: '校园卡申请', taskName: '校园卡申请', departmentName: '网络信息中心', applicant: '孙磊'},
          {tableName: '校园卡申请', taskName: '校园卡申请', departmentName: '网络信息中心', applicant: '孙磊'},
          {tableName: '校园卡申请', taskName: '校园卡申请', departmentName: '网络信息中心', applicant: '孙磊'},
          {tableName: '校园卡申请', taskName: '校园卡申请', departmentName: '网络信息中心', applicant: '孙磊'}
        ]
      }
    },
    mounted(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.classList.add("active")
    },
    destroyed(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.classList.remove("active")
    },
    components: {
      left
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";
  .cleared-wrap {
    display: flex;
    justify-content: space-between;
    margin-left: 0.2rem;
    width: 100%;
    box-sizing: border-box;
    .cleared-left {
      flex: 1;
      width: 298px;
    }
    .cleared-right {
      width: 100%;
      padding: 30px;
      margin-left: 10px;
      background: #fff;
      border-radius: 5px;
      .cleared-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 35px;
        .left {
          font-size: 0;
          .header-line {
            display: inline-block;
            height: 18px;
            width: 4px;
            background-image: linear-gradient(0deg,
              #16a4e4 0%,
              #304fea 100%),
            linear-gradient(
                #2789e3,
                #2789e3);
            background-blend-mode: normal,
            normal;
          }
          .header-content {
            display: inline-block;
            margin-left: 6px;
            line-height: 18px;
            font-size: 16px;
            font-weight: 600;
            color: #333333;
            vertical-align: top;
          }
          .service-num {
            display: inline-block;
            vertical-align: top;
            font-size: 14px;
            color: #303030;
            margin-left: 22px;
          }
        }
        .search-box {
          position: relative;
          .input-search {
            width: 330px;
            height: 32px;
            background-color: #f3f3f3;
            border-radius: 16px;
            border: none;
            padding-left: 20px;
          }
          .btn-search {
            position: absolute;
            top: 0;
            right: 0;
            width: 79px;
            height: 32px;
            background-color: #197aff;
            border-radius: 0 16px 16px 0;
            color: #fff;
            outline: none;
            border: 0;
          }
        }
      }
      .table-box {
        table {
          width: 100%;
          font-size: 14px;
          color: #444444;
          border: 1px solid #E0E0E0;
          border-radius: 5px;
          th {
            height: 40px;
            background: #1981F9;
            color: #fff;
            text-align: center;
          }
          tr:nth-child(2n) {
            background: #F5F7FA;
          }
          td {
            height: 40px;
            text-align: center;
            border-left: 1px solid #E0E0E0;
            .check {
              margin-right: 20px;
              .check-text {
                color: #848484;
                .icon-check {
                  vertical-align: middle;
                  font-size: 24px;
                  color: #197AFF;
                }
              }
            }
            .flow {
              .flow-text {
                color: #848484;
                .icon-flow {
                  vertical-align: middle;
                  font-size: 24px;
                  color: #FCAA3C;
                }
              }
            }
          }
        }
        .pages {
          margin-top: 20px;
          text-align: right;
        }
      }
    }
  }

</style>



