<template>
    <div class="right-wrap">
        <div class="submit-result">
          <p class="top"></p>
          <p class="return-msg">网络设备与服务器</p>
          <p class="prompt-msg">服务申请已经成功提交，等待审批中...请内心等待！</p>
          <p><span class="submit-btn" @click="$router.go(-2)">返回</span></p>
        </div>
    </div>

</template>

<script type="text/javascript">
export default {
  name: 'page2',
  data() {
    return {
      dialogVisible: false
    }
  },
  mounted() {
    let dom = document.querySelector('.leftbar-list>.item:nth-child(2)')
    dom.classList.add('active')
  },
  destroyed() {
    let dom = document.querySelector('.leftbar-list>.item:nth-child(2)')
    dom.classList.remove('active')
  }
}
</script>

<style lang="scss" scoped>
@import '~assets/css/lib-variable';
@import '~assets/css/lib-mixin';
.right-wrap {
  margin-left: 0.24rem;
  border: 1px solid #d5d5d5;
  display: flex;

  flex: 1;
  background: #fff;
  border-radius: 0.05rem;
  padding: 0.2rem;
  box-sizing: border-box;
  color: #000;
  .submit-result {
    display: flex;
    flex-direction: column;
    height: 4.5rem;
    justify-content: center;
    align-items: center;
    flex: 1;
    .top {
      width: 1.6rem;
      height: 1.67rem;
      background: url(../../assets/img/massage.png) no-repeat center;
      //background-size: cover;
      background-size: 100% 100%;
      margin-bottom: 0.3rem;
    }
    .return-msg {
      font-size: 0.2rem;
      font-weight: bold;
      color: #848484;
    }
    .prompt-msg {
      color: #227ce7;
      font-size: 0.16rem;
    }
  }

  .submit-btn {
    display: flex;
    width: 1rem;
    height: 0.28rem;
    font-size: 0.16rem;
    cursor: pointer;
    margin-top: 0.34rem;
    line-height: 0.28rem;
    border: 1px solid #2780e8;
    border-radius: 0.16rem;
    justify-content: center;
    color: #2b73e9;
    background: -moz-linear-gradient(left, rgba(204, 255, 255, 1) 0%, rgba(204, 204, 255, 1) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(
      left,
      rgba(204, 255, 255, 1) 0%,
      rgba(204, 204, 255, 1) 100%
    ); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(
      left,
      rgba(204, 255, 255, 1) 0%,
      rgba(204, 204, 255, 1) 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  }
}
</style>



