<template>
  <div class="wrap">
    <div class="title">
      <div class="span-left"><span class="tips"></span>待办任务</div>
    </div>
    <div class="content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="全部" name="first">
          <div class="tabbox">
            <ul>
              <li v-for="(item, index) in msglist" :key="index">
                <div class="img">
                  <img :src="item.img" alt="">
                </div>
                <div class="txt">
                  <h3>{{  item.tit }}</h3>
                  <p>{{  item.mon }}<i>{{  item.date }}</i></p>
                </div>
              </li>
            </ul>
            <div class="page">
              <el-pagination
                background
                layout="prev, pager, next"
                :total="1000">
              </el-pagination>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="OA" name="second">
          <div class="tabbox">
            <ul>
              <li v-for="(item, index) in msglist" :key="index">
                <div class="img">
                  <img :src="item.img" alt="">
                </div>
                <div class="txt">
                  <h3>{{  item.tit }}</h3>
                  <p>{{  item.mon }}<i>{{  item.date }}</i></p>
                </div>
              </li>
            </ul>
            <div class="page">
              <el-pagination
                background
                layout="prev, pager, next"
                :total="1000">
              </el-pagination>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="教务" name="third">
          <div class="tabbox">
            <ul>
              <li v-for="(item, index) in msglist" :key="index">
                <div class="img">
                  <img :src="item.img" alt="">
                </div>
                <div class="txt">
                  <h3>{{  item.tit }}</h3>
                  <p>{{  item.mon }}<i>{{  item.date }}</i></p>
                </div>
              </li>
            </ul>
            <div class="page">
              <el-pagination
                background
                layout="prev, pager, next"
                :total="1000">
              </el-pagination>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="更多" name="fourth">
          <div class="tabbox">
            <ul>
              <li v-for="(item, index) in msglist" :key="index">
                <div class="img">
                  <img :src="item.img" alt="">
                </div>
                <div class="txt">
                  <h3>{{  item.tit }}</h3>
                  <p>{{  item.mon }}<i>{{  item.date }}</i></p>
                </div>
              </li>
            </ul>
            <div class="page">
              <el-pagination
                background
                layout="prev, pager, next"
                :total="1000">
              </el-pagination>
            </div>
          </div> 
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  name: 'backlog',
  data() {
    return {
      activeName: 'first',
      msglist: [
        {img: require('../../../assets/img/content-icon-20.png'), tit: '张晓华的电脑申请', mon: '数千万人民币', date: '4月18日'},
        {img: require('../../../assets/img/content-icon-21.png'), tit: '西安地区后张预应力混泥土桥梁研究事项', mon: '3000万美元', date: '4月18日'},
        {img: require('../../../assets/img/content-icon-22.png'), tit: '基于soa的企业应用集成平台', mon: '1000万美元（估）', date: '4月18日'},
        {img: require('../../../assets/img/content-icon-23.png'), tit: '校园科技应用的审批', mon: '3000万美元', date: '4月18日'},
        {img: require('../../../assets/img/content-icon-24.png'), tit: '河北信望爱文化传播有', mon: '2亿人民币', date: '4月18日'}
      ]
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  }
}
</script>
<style lang="scss" scoped>
.wrap{
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  .content{
    box-sizing: border-box;
    padding: 20px 0;
    .tabbox{
      color: #303030;
      ul{
        li{
          height: 70px;
          overflow: hidden;
          cursor: pointer;
          border-bottom: 1px dashed #ccc;
          .img{
            float: left;
            height: 100%;
            width: 50px;
            position: relative;
            img{
              display: block;
              position: absolute;
              left: 2px;
              top: 18px;
            }
          }
          &:hover img{
            transition: all 1s linear;
            transform: rotateX(360deg);
          }
          .txt{
            float: left;
            height: 100%;
            width: calc(100% - 50px);
            h3{
              font-size: 14px;
              margin-top: 15px;
            }
            p{
              color: #848484;
              font-size: 12px;
              margin-top: 5px;
              i{
                float: right;
              }
            }
          }
        }
      }
      .page{
        width: 60%;
        float: right;
        margin-top: 20px;
      }
    }
  }
  
}
</style>



