<template>
  <div class="wrap">
    <div class="title">
      <div class="span-left"><span class="tips"></span>资源中心</div>
      <div class="span-right"><i class="iconfont icon-gengduo"></i></div>
    </div>
    <div class="content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="为您推荐" name="first">
          <div class="tabbox">
            <div class="vedio_wrap" v-for="(item, index) in list" :key="index">
              <div class="vediobox">
                <div class="vedio_link"></div>
                <div class="v_img">
                  <img :src="item.img" alt="">
                </div>
                <div class="v_cont">
                  <h3>{{ item.tit }}</h3>
                  <p class="v_team">{{ item.team }}</p>
                  <p class="v_mount">{{ item.mount }}</p>
                  <p class="v_time">{{ item.time }}</p>
                </div>
              </div>
            </div>
            <div class="loadmore">
              <span>以上是根据您的兴趣为您推荐的资源</span>
              <i>换一批</i>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="我的收藏" name="second">
          <div class="tabbox">
            <div class="vedio_wrap" v-for="(item, index) in list" :key="index">
              <div class="vediobox">
                <div class="vedio_link"></div>
                <div class="v_img">
                  <img :src="item.img" alt="">
                </div>
                <div class="v_cont">
                  <h3>{{ item.tit }}</h3>
                  <p class="v_team">{{ item.team }}</p>
                  <p class="v_mount">{{ item.mount }}</p>
                  <p class="v_time">{{ item.time }}</p>
                </div>
              </div>
            </div>
            <div class="loadmore">
              <span>以上是根据您的兴趣为您推荐的资源</span>
              <i>换一批</i>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  name: 'resourceCenter',
  data() {
    return {
      activeName: 'first',
      list: [
        {
          img: require('../../../assets/img/vedio_01.jpg'),
          tit: '生物分子学',
          team: '生物小组',
          mount: 1344,
          time: '12分钟前发布'
        },
        {
          img: require('../../../assets/img/vedio_02.jpg'),
          tit: '最好的时光·大学时代',
          team: '生物小组',
          mount: 1344,
          time: '1天前发布'
        },
        {
          img: require('../../../assets/img/vedio_03.jpg'),
          tit: '纪录片：Deeep Blue',
          team: '生物小组',
          mount: 1344,
          time: '2016年-8-26 发布'
        },
        {
          img: require('../../../assets/img/vedio_04.jpg'),
          tit: '生物分子学',
          team: '生物小组',
          mount: 1344,
          time: '2016年-8-26 发布'
        },
      ]
    }
  },
  methods: {
    handleClick(tab, event) {
      // console.log(tab, event);
    }
  }
}
</script>
<style lang="scss" scoped>
.wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  .content {
    box-sizing: border-box;
    padding: 20px 0;
    .tabbox {
      color: #303030;
      overflow: hidden;
      padding-top: 20px;
      .vedio_wrap {
        width: 50%;
        height: 168px;
        padding-bottom: 20px;
        box-sizing: border-box;
        float: left;
        position: relative;
        &:nth-of-type(2n+1){
          padding-right: 10px;
        }
        &:nth-of-type(2n){
          padding-left: 10px;
        }
        
        .vediobox {
          width: 100%;
          height: 100%;
          background: #f8fbff;
          overflow: hidden;
          box-sizing: border-box;
          border: 1px solid #c3dbfc;
          border-radius: 5px;
          position: relative;
          .vedio_link{
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0,0,0,.5);
            z-index: 1;
            cursor: pointer;
            opacity: 0;
            transition: all .3s;
            background: url('../../../assets/img/vedio-play.png') no-repeat;
            background-position: 22% 51%;
            &:hover{
              opacity: 1;
            }
          }
          .v_img {
            float: left;
            width: 52%;
            height: 100%;
            img {
              width: 100%;
              height: 100%;
            }
          }
          .v_cont {
            float: left;
            width: 48%;
            height: 100%;
            padding: 10px;
            box-sizing: border-box;
            h3{
              line-height: 35px;
            }
            p{
              text-indent: 30px;
              color: #666666;
              margin-top: 8px;
            }
            .v_team{
              background: url('../../../assets/img/vedio_icon_1.png') no-repeat;
              background-position: 0 5px;
            }
            .v_mount{
              background: url('../../../assets/img/vedio_icon_2.png') no-repeat;
              background-position: 0 5px;
            }
            .v_time{
              background: url('../../../assets/img/vedio_icon_3.png') no-repeat;
              background-position: 0 5px;
            }
          }
        }
      }
      .loadmore{
        float: left;
        text-align: right;
        margin-top: 20px;
        width: 100%;
        span{
          color: #848484;
        }
        i{
          text-indent: 20px;
          color: #333;
          background: url('../../../assets/img/vedio_icon_4.png') no-repeat;
          background-position: 0 5px;
          display: inline-block;
          margin-left: 30px;
          cursor: pointer;
        }
      }
    }
  }
}
</style>



