<template>
    <div class="page2-wrap clearfix">
      <div class="left-container">
        <div class="wrap-first">
          <leftTab></leftTab>
        </div>
        <div class="wrap-second"></div>
        <div class="wrap-three">
          <h2>办公事件</h2>
          <div class="endTime">{{ statisticalData.time }}</div>
          <ul>
            <li v-for="list in statisticalData.lists"  @click="changeVisiable()">【{{list.name}}】<span class="fr"><i>{{list.num}}</i>&ensp;件</span></li>
          </ul>
        </div>
        <div class="wrap-four">
          <h2>友情链接</h2>
          <div class="endTime">请配置友情链接</div>
          <span class="add-btn">添加</span>
        </div>
      </div>
      <div class="right-container">
        <div class="wrap-five clearfix">
            <div class="fr"></div>
            <div class=""></div>
        </div>
        <div class="wrap-six clearfix">
          <div>
            <h2>推荐服务</h2>
            <div class="server-container">
              <ul class="clearfix">
                <li v-for="list in recommendedService.lists"  @click="changeVisiable()"><img :src="list.imgUrl" alt=""><span>{{list.title}}</span></li>
              </ul>
            </div>
          </div>
          <div>
            <h2>服务排行</h2>
            <ul>
              <li v-for="(list,index) in serverRank.lists"  @click="changeVisiable()" :class="addClass(index)"><span>{{ index+1 }}</span>&nbsp;&nbsp;{{list.content}}</li>
            </ul>
          </div>
        </div>
        <div class="wrap-seven">
          <h2>审批服务<span>共{{reviewServer.total}}项服务</span></h2>
          <div class="search-btn">
            <input type="text" placeholder="输入您要搜索的关键词">
            <span><i class="iconfont icon-search"></i>&nbsp;&nbsp;搜索</span>
          </div>
          <ul class="tab-list clearfix">
            <li v-for="(list, index) in reviewServer.tabList" @click="tabClick(index)" :class="{active:tabClass(index)}">{{list}}</li>
          </ul>
          <div class="content-list">
            <ul class="clearfix">
              <li v-for="list in reviewServer.contentList"  @click="changeVisiable()"><span></span>{{list.content}}</li>
            </ul>
          </div>
          <div class="pages clearfix">
            <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="10"
              layout="prev, pager, next"
              :total="1000">
            </el-pagination>
          </div>
          <!-- 弹出框组件 -->
          <coverBox :centerDialogVisible="centerDialogVisible" @close="changeVisiable()"></coverBox>
        </div>
      </div>
    </div>

</template>

<script type="text/javascript">
import leftTab from '../cleared/left'
import coverBox from './coverBox'
  export default {
    name: 'myHome',
    data() {
      return {
        centerDialogVisible: false, //默认弹窗隐藏
        currentPage: 1, //当前页数
        dialogVisible: false,
        tabIndex: 0,  // 审批服务tab切换index
        //办公统计
        statisticalData:{
          time: '截至2016年09月19日',
          lists:[
            { name: '累计收件', num: 29 },
            { name: '累计办件', num: 13 },
            { name: '昨日收件', num: 0 },
            { name: '昨日办件', num: 0 },
            { name: '预审件', num: 4 }
          ]
        },
        // 推薦服務
        recommendedService:{
          lists:[
            { imgUrl: require('../../assets/img/server-list-01.png'), title:'一站式网上办事大厅外聘人员信息搜集'},
            { imgUrl: require('../../assets/img/server-list-02.png'), title:'门禁授权申请'},
            { imgUrl: require('../../assets/img/server-list-03.png'), title:'信息系统维护申请'},
            { imgUrl: require('../../assets/img/server-list-04.png'), title:'网络其他服务申请'},
            { imgUrl: require('../../assets/img/server-list-05.png'), title:'网络设备与服务器申请表'},
            { imgUrl: require('../../assets/img/server-list-06.png'), title:'门禁设备安装申请表'},
            { imgUrl: require('../../assets/img/server-list-07.png'), title:'办公IP申请'},
            { imgUrl: require('../../assets/img/server-list-08.png'), title:'校园卡申请'}
          ]
        },
        // 服务排行
        serverRank: {
          lists: [
            { content: '校园卡申请'},
            { content: '办卡IP申请'},
            { content: '一站式网上办卡大厅外聘人'},
            { content: '门禁设备安装申请表'},
            { content: '门禁授权申请'},
            { content: '在校生借用户口申请'},
            { content: '信息系统维护申请'},
            { content: '统招统分证明'},
            { content: '校史馆团体参观预约'},
            { content: '校园网通知公告内容发布审批'}
          ]
        },
        // 审批服务
        reviewServer: {
          total: 3,
          tabList:['全部', '网络服务类', '宣传类', '教育类', '学生事务类', '一卡通服务类', '更多'],
          contentList: [
            {content: '请假条测试'},
            {content: '在校生借用户口申请'},
            {content: '一站式网上办事大厅外聘人员信息搜集'},
            {content: '统招统分证明'},
            {content: '使用教室登记'},
            {content: '调停课申请'},
            {content: '校园网通知公告内容发布审批'},
            {content: '摄影、摄像预约'},
            {content: '开通官方微博、微信审批'},
            {content: '开通官方微博、微信审批'},
          ]
        }
      };
    },
    destroyed(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.classList.remove("active")
    },
    mounted(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.setAttribute('class', 'item active')
      // bananer动画
      let txt = document.querySelector('.wrap-five').querySelector('div:nth-child(2)');
      txt.setAttribute('class','text-move');
      // 左侧我的服务组件 leftTab 隐藏多余部分
      let dsNone = document.querySelector('.friendship-link');
      dsNone.setAttribute('style','display:none');
    },
    methods:{
      addClass(index){
        if(index == 0){
          return 'colorOne'
        }else if(index == 1){
          return 'colorTwo'
        }else if(index == 2){
          return 'colorThree'
        }else{
          return '';
        }
      },
      tabClass(index){
        if(index == this.tabIndex){
          return true;
        }else{
          return false;
        }
      },
      tabClick(index){ //  tab切换
        this.tabIndex = index
        this.messOpen(this.reviewServer.tabList[index])
        if(this.reviewServer.tabList[index] == '更多'){
          this.$router.push('/officeHall/moreServe');
        }
      },
      open(){
        this.$alert('', 'HTML 片段', {
          customClass:'cover',
          dangerouslyUseHTMLString: true
        });
      },
      messOpen(tab) {
        this.$message({message:tab,duration:1000});
      },
      changeVisiable(){
        this.centerDialogVisible = !this.centerDialogVisible
      },
      handleSizeChange(){

      },
      handleCurrentChange(){

      }
    },
    components:{
      leftTab,
      coverBox
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";
  .page2-wrap{
    width: 100%;
    background: #f2f2f2;
    margin-left: 0.2rem;
    flex-wrap: wrap;
    justify-content: space-between;
    display: flex;
    .wrap-first,.wrap-five{
      margin-bottom: 10px;
      height: 350px;
    }
    .wrap-first{
      padding: 0;
      .friendship-link{
        display: none;
      }
    }
    .wrap-five{
      position: relative;
      background: url(../../assets/img/office-hall-bananer.jpg);
      background-size: 100% 100%;
      &>div:nth-child(1){
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 50%;
        background: linear-gradient(to right,transparent,rgba(26,150,229,.8), #1a96e5, #1a96e5 )
      }
      &>div:nth-child(2){
        position: absolute;
        right: 0;
        top: 0;
        background: url(../../assets/img/text-move.png) no-repeat center;
        width: 50%;
        height: 100%;

      }
      .text-move{
        animation: textMove 2s linear;
      }
      @keyframes textMove
      {
        from {transform: rotateY(360deg);opacity: 0;}
        to {transform: rotateY(0deg);opacity: 1;}
      }
    }
    .wrap-second{
      margin-bottom: 10px;
      height: 145px;
      background: url(../../assets/img/report-fix.jpg) no-repeat;
      background-size: 100% 100%;
      border-color: #3e8fff;
    }
    .wrap-three{
      margin-bottom: 10px;
      height: 310px;
      .endTime{
        color: #848484;
        padding-left: 5px;
        font-size: 14px;
        margin-top: 10px;
        line-height: 35px;
        border-bottom: 1px dashed #cecfd4;
      }
      li{
        line-height: 35px;
        cursor: pointer;
        padding-right: 5px; 
        color: #333;
        font-size: 14px;
        border-bottom: 1px dashed #cecfd4;
      }
      li:hover{
        transition: all .3s linear;
        background: #eee;
        transform: rotateX(360deg);
      }
      span{
        line-height: 35px;
        font-size: 14px;
        i{
          color: #197aff;
        }
      }
    }
    .wrap-four{
      position: relative;
      height: 112px;
      .endTime{
        color: #848484;
        padding-left: 5px;
        font-size: 14px;
        margin-top: 10px;
        line-height: 35px;
      }
      .add-btn{
        position: absolute;
        right: 30px;
        top: 30px;
        display: block;
        width: 60px;
        height: 28px;
        border-radius: 15px;
        background: #d2e8fa;
        color: #2f53ea;
        font-size: 14px;
        border: 1px solid #2f53ea;
        text-align: center;
        line-height: 30px;
      }
    }
    .wrap-six{
      box-sizing: border-box;
      width: 100%;
      margin-bottom: 10px;
      border-radius: 0;
      border: none;
      background: transparent;
      box-shadow: 0 0 0 0 transparent;
      &>div{
        box-sizing: border-box;
        float: left;
        padding: 30px;
        border-radius: 5px;
        border: 1px solid #d5d5d5;
        background: #fff;
        box-shadow: 1px 1px 1px #ccc;
        h2{
          color: #333;
          font-size: 16px;
          padding-left: 6px;
          border-left: 4px solid #304fea;
          line-height: 18px;
        }
        &:nth-child(1){
          width: calc(71% - 10px);
          margin-right: 10px;
          height: 475px;
          .server-container{
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            padding: 10px 0 20px;
            background: url(../../assets/img/line-bg.png) no-repeat;
            background-size: 100% 100%;
          }
          li{
            width: 25%;
            float: left;
            box-sizing: border-box;
            text-align: center;
            cursor: pointer;
            height: 200px;
            padding: 20px 20px;
            img{
              cursor: pointer;
            }
            &>img:hover{
              animation: imgMove 1s linear;
            }
            @keyframes imgMove
            {
              from {transform: rotateY(0deg);opacity: 0;}
              to {transform: rotateY(360deg);opacity: 1;}
            }
            &>span{
              display: block;
              text-align: center;
              font-size: 16px;
              color: #333;
            }
          }
        }
        &:nth-child(2){
          width: 29%;
          height: 475px;
          ul{
            padding: 30px 0;
            box-sizing: border-box;
          }
          li{
            color: #333;
            font-size: 14px;
            cursor: pointer;
            line-height: 35px;
            padding: 0 5px;
            border-bottom: 1px dashed #cecfd4;
            span{
              display: inline-block;
              line-height: 22px;
              width: 22px;
              margin-right: 5px;
              border-radius: 50%;
              text-align: center;
            }
            &.colorOne{
              span{
                background: #ff8e19;
                color: #fff;
              }
            }
            &.colorTwo{
              span{
                background: #ffb619;
                color: #fff;
              }
            }
            &.colorThree{
              span{
                background: #6fb2ef;
                color: #fff;
              }
            }
          }
          li:hover{
            transition: all .2s linear;
            background: #eee;
            font-size: 16px;
            // transform: rotateX(360deg);
          }
        }
      }
    }
    .wrap-seven{
      position: relative;
      box-sizing: border-box;
      padding: 30px;
      height: 570px;
      .search-btn{
        position: absolute;
        right: 35px;
        top: 25px;
        width: 330px;
        height: 32px;
        &>span{
          position: absolute;
          right: 0;
          top: 0;
          display: block;
          height: 32px;
          line-height: 32px;
          text-align: center;
          width: 80px;
          color: #fff;
          background: #197aff;
          border-top-right-radius: 16px;
          border-bottom-right-radius: 16px;
        }
      }
      input{
        border: none;
        width: 300px;
        height: 30px;
        padding-left: 10px;
        border: 1px solid #f3f3f3;
        border-radius: 16px;
        background: #f3f3f3;
      }
      .tab-list{
        margin-top: 30px;
        li{
          float: left;
          color: #333;
          line-height: 32px;
          padding: 0 20px;
          margin-right: 15px;
          border-radius: 16px;
          border: 1px solid #bbb;
          cursor: pointer;
          &.active{
            border-color: #197aff;
            background: #f5f7fa;
          }
          &:hover{
            animation: hoverAnimation 1s linear;
            transition: all 1s linear;
            border-color: #197aff;
            background: #f5f7fa;
          }
          @keyframes hoverAnimation {
            0%{
              transform: translateX(-10px);
            }
            20%{
              transform: translateX(0px);
            }
            30%{
              transform: translateX(10px);
            }
            50%{
              transform: translateX(0px);
            }
            70%{
              transform: translateX(-5px);
            }
            80%{
              transform: translateX(0px);
            }
            90%{
              transform: translateX(5px);
            }
            100%{
              transform: translateX(0px);
            }
          }
        }
      }
      .content-list{
        ul{
          padding: 40px 0 0;
        }
        li{
          margin-right: 4%;
          width: 48%;
          cursor: pointer;
          float: left;
          color: #303030;
          font-size: 14px;
          border-bottom: 1px dashed #cecfd4;
          line-height: 65px;
          &:nth-child(2), &:nth-child(4), &:nth-child(6), &:nth-child(8), &:nth-child(10){
            width: 48%;
            margin-right: 0;
          }
          &>span{
            margin-right: 28px;
            display: inline-block;
            width: 34px;
            height: 34px;
            vertical-align: middle;
          }
          &:nth-child(1)>span{
            background:url(../../assets/img/content-icon-01.png);
          }
          &:nth-child(2)>span{
            background:url(../../assets/img/content-icon-02.png);
          }
          &:nth-child(3)>span{
            background:url(../../assets/img/content-icon-03.png);
          }
          &:nth-child(4)>span{
            background:url(../../assets/img/content-icon-04.png);
          }
          &:nth-child(5)>span{
            background:url(../../assets/img/content-icon-05.png);
          }
          &:nth-child(6)>span{
            background:url(../../assets/img/content-icon-06.png);
          }
          &:nth-child(7)>span{
            background:url(../../assets/img/content-icon-07.png);
          }
          &:nth-child(8)>span{
            background:url(../../assets/img/content-icon-08.png);
          }
          &:nth-child(9)>span{
            background:url(../../assets/img/content-icon-09.png);
          }
          &:nth-child(10)>span{
