      font-size: 16px;
      padding-left: 6px;
      border-left: 4px solid #304fea;
      line-height: 18px;
      &>span{
        line-height: 18px;
        margin-left: 50px;
        font-size: 14px;
        font-weight: normal;
      }
    }
  }
  .left-container{
    margin-right: 10px;
    flex: 1;
    &>div{
      padding: 30px;
      box-sizing: border-box;
      border-radius: 5px;
      //border: 1px solid #d5d5d5;
      background: #fff;
      box-shadow: 1px 1px 1px #ccc;
    }
  }
  .right-container{
    flex: 4;
    &>div{
      border-radius: 5px;
      border: 1px solid #d5d5d5;
      background: #fff;
      box-shadow: 1px 1px 1px #ccc;
    }
  }
</style>



