<template>
  <div class="wrap">
    <div class="title">
      <div class="span-left"><span class="tips"></span>活动中心</div>
      <div class="span-right"><i class="iconfont icon-gengduo"></i></div>
    </div>
    <div class="content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="热闹活动" name="first">
          <div class="tabbox">
            <div class="vedio_wrap" v-for="(item, index) in list" :key="index">
              <div class="vediobox">
                <div class="v_img">
                  <img :src="item.img" alt="">
                </div>
                <div class="v_cont">
                  <h3>{{ item.tit }}</h3>
                  <p class="c4">{{ item.pub }}</p>
                  <p class="c4">{{ item.time }}</p>
                  <p class="c9 mt5">{{ item.jion }}</p>
                  <p class="c9">{{ item.intre }}</p>
                </div>
              </div>
            </div>
            <div class="loadmore">
              <i>换一批</i>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="我关注的人的活动" name="second">
          <div class="tabbox">
            <div class="vedio_wrap" v-for="(item, index) in list" :key="index">
              <div class="vediobox">
                <div class="v_img">
                  <img :src="item.img" alt="">
                </div>
                <div class="v_cont">
                  <h3>{{ item.tit }}</h3>
                  <p class="c4">{{ item.pub }}</p>
                  <p class="c4">{{ item.time }}</p>
                  <p class="c9 mt5">{{ item.jion }}</p>
                  <p class="c9">{{ item.intre }}</p>
                </div>
              </div>
            </div>
            <div class="loadmore">
              <i>换一批</i>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  name: 'resourceCenter',
  data() {
    return {
      activeName: 'first',
      list: [
        {
          img: require('../../../assets/img/active_01.jpg'),
          tit: '[活动]写出你心中的中秋—中秋节征文',
          pub: '校园杂志社',
          time: '发布于2016-8-30 15：11：35',
          jion: '',
          intre: ''
        },
        {
          img: require('../../../assets/img/active_02.jpg'),
          tit: '[问卷]毕业季你的选择是什么？',
          pub: '校园杂志社',
          time: '发布于2016-8-30 15：11：35',
          jion: '参与：12,244',
          intre: '感兴趣：441,233'
        },
        {
          img: require('../../../assets/img/active_03.jpg'),
          tit: '[活动]名家讲坛—张教师来我校开讲啦！',
          pub: '校园杂志社',
          time: '发布于2016-8-30 15：11：35',
          jion: '参与：12,244',
          intre: '感兴趣：441,233'
        },
        {
          img: require('../../../assets/img/active_04.jpg'),
          tit: '[投票]校园之星评选中！',
          pub: '校园杂志社',
          time: '发布于2016-8-30 15：11：35',
          jion: '参与：12,244',
          intre: '感兴趣：441,233'
        },
      ]
    }
  },
  methods: {
    handleClick(tab, event) {
      // console.log(tab, event);
    }
  }
}
</script>
<style lang="scss" scoped>
.wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  .content {
    box-sizing: border-box;
    padding: 20px 0;
    .tabbox {
      color: #303030;
      overflow: hidden;
      padding-top: 20px;
      .vedio_wrap {
        width: 50%;
        height: 180px;
        padding-bottom: 20px;
        box-sizing: border-box;
        float: left;
        position: relative;
        cursor: pointer;
        &:hover{
          .v_img{
            animation: vImgAnima .5s linear;
          }
          @keyframes vImgAnima {
            from {
              opacity: 0;
              transform: translateX(-100%);
            }
            to {
              opacity: 1;
              transform: translateX(0%);
            }
          }
        }
        &:nth-of-type(2n+1){
          padding-right: 10px;
        }
        &:nth-of-type(2n){
          padding-left: 10px;
        }
        
        .vediobox {
          width: 100%;
          height: 100%;
          overflow: hidden;
          box-sizing: border-box;
          border-radius: 5px;
          position: relative;
          .v_img {
            float: left;
            width: 40%;
            height: 100%;
            img {
              display: block;
              width: calc(100% - 5px);
              height: 100%;
              border-radius: 5px;
            }
          }
          .v_cont {
            float: left;
            width: 60%;
            height: 100%;
            padding: 10px 20px;
            box-sizing: border-box;
            background: #f3f8ff;
            border-radius: 5px;
            h3{
              border-bottom: 1px solid #ccc;
              margin-bottom: 10px;
              padding-bottom: 5px;
            }
            p{
              font-size: 12px;
            }
            .c4{
              color: #444;
            }
            .c9{
              color: #999;
            }
            .mt5{
              margin-top: 5px;
            }
          }
        }
      }
      .loadmore{
        float: left;
        text-align: right;
        margin-top: 10px;
        width: 100%;
        i{
          text-indent: 20px;
          color: #333;
          background: url('../../../assets/img/vedio_icon_4.png') no-repeat;
          background-position: 0 5px;
          display: inline-block;
          margin-left: 30px;
          cursor: pointer;
        }
      }
    }
  }
}
</style>



