<template>
  <div class="right-wrap">
    <div class="submit-result">
      <p class="top"></p>
      <p class="prompt-msg">页面建设中...请耐心等待！</p>
    </div>
  </div>

</template>

<script type="text/javascript">

  export default {
    name: 'page2',
    data() {
      return {
        dialogVisible: false
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";
  .right-wrap{
    width: 100%;
    display: flex;
    background: #f2f2f2;
    margin-left: 0.2rem;
    flex-wrap: wrap;
    justify-content: space-between;
    border: 1px solid #d5d5d5;
    background: url(../../assets/img/building.gif) no-repeat center;
    //background-size: cover;
    background-size: 100% 100%;
    background: #fff;
    border-radius: 0.05rem;
    box-sizing: border-box;
    color: #000;
    .submit-result{
      display: flex;
      flex-direction: column;
      height:4.5rem;
      justify-content: center;
      align-items: center;
      flex:1;
      .top{
        width:1.6rem;
        height:1.67rem;
        background: url(../../assets/img/building.gif) no-repeat center;
        background-size: 100% 100%;
        margin-bottom: 0.3rem;
      }
    }
    .prompt-msg{
      font-size:0.16rem;
      color: #666;
    }
  }

</style>



