<template>
  <div class="wrap">
    <div class="pick_wrap">
      <el-date-picker
      v-model="dataObj"
      type="date"
      placeholder="选择日期"
      id="mypicker"
      @change="dateSelect"
      >
      </el-date-picker>
    </div>
    <div class="head" @click="changeDate">
      <div class="date_left">
        <div class="date_day">
          {{ date.day }}
        </div>
        <div class="date_month">
          {{ date.month }}月
        </div>
      </div>
      <div class="date_right">
        <div class="date_detail">
          {{ date.year }}年{{ date.month }}月{{ date.day }}日
        </div>
        <div class="date_lunar">
          农历三月初四
        </div>
        <div class="date_teach">
          第十六教学周
        </div>
      </div>
    </div>
    
    <div class="section">
      <h2>日程事件·{{ date.year }}年{{ date.month }}月{{ date.day }}日</h2>
      <ul>
        <li v-for="(item, index) in list" :key="index">{{ item }}</li>
        <li class="more">查看全部日程事件</li>
      </ul>
    </div>
  </div>
    
</template>
<script type="text/javascript">
export default {
  name: 'schedule',
  data() {
    return {
      dataObj: '',
      date: {
        day: '9',
        month: '5',
        year: '2018'
      },
      list: [
        '【毕业季】2017届优秀毕业生达人风采展',
        '【毕业季】2017届优秀毕业生达人',
        '【毕业季】2016届优秀毕业生达人风采展',
        '【毕业季】2016届优秀毕业生达人',
        '【毕业季】毕业之歌大合唱',
        '【毕业季】2017届优秀毕业生达人风采展',
        '【毕业季】2017届优秀毕业生',
        '【毕业季】2017届优秀毕业生达人风采展',
        '【毕业季】2017届优秀毕业生',
      ]
    }
  },
  methods: {
    changeDate(){
      let pick = document.getElementById('mypicker');
      pick.focus();
    },
    dateSelect(data) {
      let year = data.getFullYear();
      let month = data.getMonth()+1;
      let day = data.getDate();
      this.date = {day, month, year};
    }
  }
}
</script>
<style lang="scss" scoped>
.wrap {
  width: 100%;
  height: 100%;
  position: relative;
  .pick_wrap{
    opacity: 0;
    position: absolute;
    background: #ccc;
    left: 350px;
    top: 10px;
  }
  .head {
    background: #d3e5fa;
    height: 110px;
    cursor: pointer;
    overflow: hidden;
    .date_left{
      float: left;
      width: 110px;
      height: 100%;
      color: #007aff;
      text-align: center;
      padding: 15px;
      box-sizing: border-box;
      .date_day{
        font-size: 48px;
        font-weight: bolder;
        border-bottom: 1px solid #007aff;
        line-height: 50px;
      }
      .date_month{
        font-size: 20px;
        line-height: 30px;
      }
    }
    .date_right{
      float: left;
      height: 100%;
      width: 150px;
      color: #303030;
      padding: 20px 0;
      .date_detail{
        font-size: 14px;
      }
      .date_lunar{
        font-size: 12px;
      }
      .date_teach{
        font-size: 14px;
        line-height: 3em;
      }
    }
  }
  .section{
    height: calc( 100% - 110px);
    box-sizing: border-box;
    padding: 20px;
    color: #303030;
    background: url(~assets/img/sch_bg.png) no-repeat;
    background-position: 400px bottom;
    h2{
      font-size: 14px;
      line-height: 38px;
    }
    ul{
      li{
        cursor: pointer;
        font-size: 14px;
        line-height: 38px;
        text-indent: 20px;
        background: url(~assets/img/list-point.png) no-repeat;
        &:hover{
          color: #197aff;
        }
        &.more{
          color: #007aff;
          cursor: pointer;
          background: url(~assets/img/list-more.png) no-repeat;
          text-indent: 25px;
          text-decoration: underline;
        }
      }
    }
  }
}
</style>




