<template>
  <div class="wrap">
    <div class="title">
      <div class="span-left"><span class="tips"></span>办事大厅</div>
      <router-link to="/officeHall/myHome">
        <div class="span-right"><i class="iconfont icon-gengduo"></i></div>
      </router-link>
    </div>
    <div class="bar">
      <router-link tag="div" class="bar1" to="/index/applying"><span class="iconfont icon-daiban"></span><i>5</i>待办事项</router-link>
      <router-link tag="div" class="bar2" to="/index/cleared"><span class="iconfont icon-history"></span><i>15</i>办理中</router-link>
      <router-link tag="div" class="bar3" to="/index/approval"><span class="iconfont icon-yiwancheng"></span><i>665</i>  已办理</router-link>
    </div>
    <h2>推荐服务</h2>
    <div class="recommendBox">
        <dl @click="changeVisiable">
          <dt><img src="~assets/img/tj1.png"></dt>
          <dd>教室借用申请</dd>
        </dl>
        <dl @click="changeVisiable">
          <dt><img src="~assets/img/tj2.png"></dt>
          <dd>校园卡银校转帐问题处理</dd>
        </dl>
        <dl @click="changeVisiable">
          <dt><img src="~assets/img/tj3.png"></dt>
          <dd>门禁授权申请</dd>
        </dl>
        <dl @click="changeVisiable">
      <dt><img src="~assets/img/tj4.png"></dt>
      <dd>档案借阅/外界申请和审批</dd>
    </dl>
      <dl @click="changeVisiable">
        <dt><img src="~assets/img/tj5.png"></dt>
        <dd>学生综合评价</dd>
      </dl>
      <dl @click="changeVisiable">
        <dt><img src="~assets/img/tj1.png"></dt>
        <dd>教室借用申请</dd>
      </dl>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  name: 'Investment',
  data() {
    return {}
  },
  methods: {
    changeVisiable(){
      this.$emit('opendlg')
    }
  }
}
</script>
<style lang="scss" scoped>
.wrap {
  padding: 20px;
  box-sizing: border-box;

  .title {
    display: flex;
    justify-content: space-between;
    height: 0.3rem;
    align-items: center;
    width: 100%;
    .span-left {
      font-size: 16px;
      font-weight: bold;
      letter-spacing: 0px;
      display: flex;
      align-items: center;
      color: #333333;
      .tips {
        width: 4px;
        height: 18px;
        background-image: linear-gradient(0deg, #16a4e4 0%, #304fea 100%), linear-gradient(#2789e3, #2789e3);
        background-blend-mode: normal, normal;
        display: inline-block;
        margin-right: 0.1rem;
      }
    }
    .span-right {
      i.icon-gengduo:before {
        color: #197aff;
        font-size: 28px;
      }
    }
  }

  .bar {
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
    display: flex;
    justify-content: space-between;
    & > div{
      width: 2.38rem;
      height: 1.1rem;
      font-size: 0.14rem;
      color: #333333;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        font-style: normal;
        font-size: 0.32rem;
        display: inline-block;
        /* margin-right: .1rem;
           margin-left: .1rem;*/
      }
      &.bar1 {
        background: url(~assets/img/bar1.png) no-repeat;
        background-size: 100% 100%;
        cursor: pointer;
        i {
          color: #2b73e9;
        }
        span.icon-daiban:before {
          color: #16a4e4;
          font-size: 0.34rem;
          margin-right: 0.05rem;
        }
      }
      &.bar2 {
        background: url(~assets/img/bar2.png) no-repeat;
        background-size: 100% 100%;
        cursor: pointer;
        i {
          color: #ff762b;
        }
        span.icon-history:before {
          color: #ff722b;
          font-size: 0.34rem;
          margin-right: 0.05rem;
        }
      }
      &.bar3 {
        background: url(~assets/img/bar3.png) no-repeat;
        background-size: 100% 100%;
        cursor: pointer;
        i {
          color: #10a662;
        }
        span.icon-yiwancheng:before {
          color: #008a58;
          font-size: 0.34rem;
          margin-right: 0.05rem;
        }
      }
    }
  }

  h2 {
    border-bottom: 1px solid #197aff;
    color: #333333;
    font-size: 0.16rem;
    line-height: 41px;
    width: 100%;
  }

  .recommendBox {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding-top: 0.2rem;
    dl {
      border-right: 1px solid #e8e8e8;
      flex-grow: 1;
      padding-bottom: 0.2rem;
      width: 33.2%;
      text-align: center;
      cursor: pointer;
      &:nth-child(3n) {
        border: none;
      }
      dt {
        border-radius: 50%;
        width: 1rem;
        margin: 0px auto;
        height: 1rem;
        @keyframes imgMove{
          from {transform: rotateY(0deg);opacity: 0;}
          to {transform: rotateY(360deg);opacity: 1;}
        }
        img {
          width: 100%;
          &:hover{
            animation: imgMove 1s linear;
          }
        }
      }
      dd {
        font-size: 0.16rem;
        color: #333333;
        margin-top: 0.1rem;
      }
    }
  }
}
.bar>div{
    &:nth-child(1):hover{
      box-shadow: 0 0 10px blue;
      border-radius: 6px;
    }
    &:nth-child(2):hover{
      box-shadow: 0 0 10px orange;
      border-radius: 6px;
    }
    &:nth-child(3):hover{
      box-shadow: 0 0 10px green;
      border-radius: 6px;
    }
}
</style>



