<template>
  <div class="homeWrap">
    <div class="child">
      <investment @opendlg="changeVisiable"></investment>
    </div>
    <div class="child">
      <schedule></schedule>
    </div>
    <div class="child">
      <backlog></backlog>
    </div>
    <div class="child">
      <notice></notice>
    </div>
    <div class="child">
      <resource-center></resource-center>
    </div>
    <div class="child">
      <exchange-center></exchange-center>
    </div>
    <div class="child">
      <activity-center></activity-center>
    </div>
    <div class="child">
      <data-analysis :load="bol"></data-analysis>
    </div>
    <coverBox :centerDialogVisible="centerDialogVisible" @close="changeVisiable()"></coverBox>
  </div>


</template>

<script type="text/javascript">
import investment from './components/Investment'
import schedule from './components/schedule'
import backlog from './components/backlog'
import notice from './components/notice'
import resourceCenter from './components/resourceCenter'
import exchangeCenter from './components/exchangeCenter'
import activityCenter from './components/activityCenter'
import dataAnalysis from './components/dataAnalysis'
import coverBox from '../officeHall/coverBox'
export default {
  name: 'home',
  data() {
    return {
      bol: false,
      centerDialogVisible: false
    }
  },
  methods: {
    changeVisiable(){
      this.centerDialogVisible = !this.centerDialogVisible
    },
  },
  beforeRouteEnter: (to, from, next) => {
    next(vm => {
      vm.bol = true
      window.onload = function(){
        if(vm.bol){
          vm.bol = false
        }
        setTimeout(function(){
          vm.bol = true
        },0)
      }
    });
  },
  components: {
    investment,
    schedule,
    backlog,
    notice,
    resourceCenter,
    exchangeCenter,
    activityCenter,
    dataAnalysis,
    coverBox
  }
}
</script>

<style lang="scss" scoped>
.homeWrap {
  width: 100%;
  background: #f2f2f2;
  margin-left: 0.2rem;
  overflow: hidden;
  & > .child {
    float: left;
    width: 48%;
    height: 570px;
    background: #fff;
    margin-bottom: 1.5%;
    margin-right: 1.5%;
    box-shadow: 0px 5px 24px 0px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    border: solid 1px #d5d5d5;
    /deep/ .content {
      height: calc(100% - 30px);
    }
    /deep/ .title {
      display: flex;
      justify-content: space-between;
      height: 0.3rem;
      align-items: center;
      width: 100%;
      .span-left {
        font-size: 16px;
        font-weight: bold;
        letter-spacing: 0px;
        display: flex;
        align-items: center;
        color: #333333;
        .tips {
          width: 4px;
          height: 18px;
          background-image: linear-gradient(0deg, #16a4e4 0%, #304fea 100%), linear-gradient(#2789e3, #2789e3);
          background-blend-mode: normal, normal;
          display: inline-block;
          margin-right: 0.1rem;
        }
      }
      .span-right {
        cursor: pointer;
        i.icon-gengduo:before {
          color: #197aff;
          font-size: 28px;
        }
      }
    }
  }
}
</style>



