<template>
  <div class="wrap">
    <div class="title">
      <div class="span-left"><span class="tips"></span>数据分析</div>
      <div class="span-right"><i class="iconfont icon-gengduo"></i></div>
    </div>
    <div class="content">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="一周教育指数" name="first">
        </el-tab-pane>
        <el-tab-pane label="学生体验排行" name="second">
        </el-tab-pane>
        <el-tab-pane label="学生成绩分布" name="third">
        </el-tab-pane>
        <el-tab-pane label="升学率分析" name="fourth">
        </el-tab-pane>
      </el-tabs>
      <div class="tabbox">
        <linechart v-if="load"></linechart>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import linechart from 'components/echarts/line'
export default {
  name: 'dataAnalysis',
  props: ['load'],
  data() {
    return {
      activeName: 'first',
      // load: false
    }
  },
  methods: {
    handleClick(tab, event) {
      // console.log(tab, event);
    }
  },
  mounted() {

  },

  components: {
    linechart
  }
}
</script>
<style lang="scss" scoped>
.wrap {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  .content {
    box-sizing: border-box;
    padding: 20px 0;
    .tabbox {
      color: #303030;
      overflow: hidden;
      padding-top: 20px;
      height: 400px;
      width: 100%;
    }
  }
}
</style>



