<template>
  <div class="login-wrap">
      <div class="log-content">
          <h2 class="login-title">登录</h2>
          <div class="login-message">
            <form action="###" class="login-form">
                <div class="input-wrap">
                  <i class="iconfont  icon-yonghu"></i>
                  <input type="text" name="user" id="user" placeholder="学号/工号/用户邮箱" autocomplete="off"/>
                </div>
                <div class="input-wrap">
                  <i class="iconfont  icon-mima"></i>
                  <input type="password" name="psd" placeholder="请输入密码" autocomplete="off"/>
                </div>


              <div class="sure-btn">
                <el-checkbox v-model="checked">记住密码</el-checkbox>
                <a href="###">忘记密码</a>
              </div>
              <span class="submit-btn" @click="$router.push('/index/home')">提交</span>
            </form>
          </div>
        <div class="help-msg">
          <p class="help-title">温馨提示</p>
          <p class="help-title-con">1.学会账号为学号，教职工账号为教职工编号</p>
          <p class="help-title-con">2.初始密码均为上网账号密码</p>
        </div>
      </div>
  </div>

</template>

<script type="text/javascript">
  import theme from 'components/theme/theme'
  export default {
    name: 'home',
    data(){
      return {
        checked: true
      }
    },
    methods:{

    },
    components:{
       theme
    }
   }
</script>

<style lang="scss" scoped>
  @import "~assets/css/lib-variable";
  @import "~assets/css/lib-mixin";

  //框架整体布局
  .login-wrap{
    position: relative;
    width: 100%;
    height: 100%;
    background: url(../../assets/img/login-bg.jpg) no-repeat center;
    //background-size: cover;
    background-size: 100% 100%;
      .log-content{
        height:4.55rem;
        width:4.15rem;
        border-radius: 5px;
        border:1px solid #3293ff;
        position: absolute;
        right:16.5%;
        top:25%;
        display: flex;
        background: #fff;
        flex-direction: column;
        .login-title{
        height:0.8rem;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        letter-spacing: 0.24rem;
        font-size: 0.24rem;
        color: #ffffff;
        text-align: center;
        background: -moz-linear-gradient(left, rgba(51,102,255,1) 0%, rgba(51,153,204,1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(left, rgba(51,102,255,1) 0%, rgba(51,153,204,1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(left, rgba(51,102,255,1) 0%, rgba(51,153,204,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      }
        .login-message{
          display: flex;
          flex-direction: column;
          .login-form{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            .input-wrap{
              position: relative;
            }
            .iconfont{
              position: absolute;
              left:0.16rem;
              top:0.34rem;
              color: #2d5ae9;
              font-size:0.18rem;
            };
          }
          input{
            width:2.92rem;
            height:0.36rem;
            border:1px solid #d4d4d4;
            border-radius: 0.16rem;
            padding-left:0.42rem;
            box-sizing: border-box;
            margin-top:0.27rem;
            background-color: #f3f3f3!important;
            color: #8c8c8c;
          }
          .sure-btn{
            height:0.8rem;
            display: inline-flex;
            width:2.92rem;
            align-items: center;
            justify-content: space-between;
          }
          .submit-btn{
            width:2.92rem;
            height:0.36rem;
            font-size:0.16rem;
            cursor: pointer;
            line-height:0.36rem;
            border:1px solid #2780e8;
            border-radius: 0.16rem;
            margin-bottom:0.22rem;
            text-align: center;
            color: #2b73e9;
            background: -moz-linear-gradient(left, rgba(204,255,255,1) 0%, rgba(204,204,255,1) 100%); /* FF3.6-15 */
            background: -webkit-linear-gradient(left, rgba(204,255,255,1) 0%, rgba(204,204,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(left, rgba(204,255,255,1) 0%, rgba(204,204,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
          }
        }
        .help-msg{
          display: flex;
          background-color: #f4f4f4;
          flex-direction: column;
          align-items: center;
          flex:1;
          p{
            width:2.92rem;
            color: #959595;
            height:0.24rem;
          }
          .help-title{
            font-size:0.18rem;
            height:0.48rem;
            line-height: 0.48rem;
          }
          .help-title-con{
            font-size:0.14rem;
          }
        }
      }
  }
</style>



