<template>
  <div class="cover">
    <el-dialog
        title="网络设备与服务器申请表"
        :visible.sync="centerDialogVisible"
        width="60%"
        :before-close="close">
        <div class="cover-content">
            <h3>服务简介</h3>
            <div><strong>1、申请人范围：</strong>信息干事</div>
            <div><strong>2、流程图：</strong></div>
            <div class="pic-pidding">
                <ul class="clearfix">
                    <li v-for="(list,index) in lists">
                        <img :src="list.imgUrl" width="95" alt="">
                        <span class="dot" v-if="!(index==lists.length - 1)"></span>
                        <span class="title">{{list.text}}</span>
                    </li>
                </ul>
            </div>
            <div><strong>3、流程说明：</strong></div>
            <div>   
                <p>1、申请单位根据需求做好前期调研工作，信息干事在线填写申请表，详细阐明所需设备的用途、基本配置要求等内容，确保所填信息真实、准确后，在线提交申请表。</p>
                <p>2、网络信息中心对申请单位的需求进行技术审核，根据学校网络及硬件设施情况采取相应的技术措施，统一规划部署，有限通过虚拟空间、虚拟主机、独立主机、集群主机等
     方式，为各单位、部门提供技术支持和保障。如需设备采购，严格按照学校规定的采购程序进行。涉及全校范围的硬件设备由网络信息中心统一安放、集中管理，涉及各        
     单位 、部门内部使用的设备（如机房和实验室的网络设备、服务器等）由各单位、部门自行管理。</p>
                <p>3、关于申请表的处理情况和流转进度，信息干事可登录一站式服务大厅查看。</p>
            </div>
        </div>
        <div class="enter-btn">
            <button @click="goon">进入服务</button>
        </div>
        
    </el-dialog>
  </div>
</template>

<script>
export default {
   name: 'coverBox',
   props: ['centerDialogVisible'],
   data() {
      return {
          lists: [
              { text:'填写申请单', imgUrl: require('../../assets/img/cover_01.png')},
              { text:'申请单位审批', imgUrl: require('../../assets/img/cover_02.png')},
              { text:'网络部初审', imgUrl: require('../../assets/img/cover_03.png')},
              { text:'网络信息中心领导审核', imgUrl: require('../../assets/img/cover_04.png')},
              { text:'网络不处理', imgUrl: require('../../assets/img/cover_05.png')},
              { text:'返回结果', imgUrl: require('../../assets/img/cover_06.png')}
          ]
      };
    },
    mounted(){
        console.log(this.centerDialogVisible)
    },
    methods:{
        close(){
            this.$emit('close')
        },
        goon(){
            this.$router.push('/officeHall/fillForm')
        }
    }
}
</script>

<style lang="scss" scoped>
.cover{
    color: #333;
}
.cover-content{
    border-top: 1px solid #e6e6e6;
    h3{
        font-size: 16px;
        color: #333;
        padding: 20px 0 30px;
        font-weight: normal;
    }
    &>div{
        strong{
            color: #000;
        }
        font-size: 14px;
        color: #333;
        p{
           font-size: 14px;
           line-height: 30px;
           padding-left:25px; text-indent:-25px;
        }
        &:last-child{
            margin-top: 15px;
        }
    }
    .pic-pidding{
        padding: 30px 65px 40px 45px;
        ul{
            overflow: hidden;
        }
        li{
            float: left;
            &:nth-child(6){
                .title{
                    margin-left: 0;
                }
            }
        }
        .dot{
            display: inline-block;
            width: 81px;
            height: 95px;
            background: url(../../assets/img/cover_07.png) no-repeat center center;
        }
        .title{
            display: block;
            margin-top: 10px;
            color: #2b73e9;
            font-size: 14px;
            width: 100%;
            text-align: center;
            margin-left: -45px;
        }
    }
}
.enter-btn{
    margin-top: 40px;
    text-align: center;
    button{
        width: 108px;
        height: 38px;
        color: #2b73e9;
        font-size: 14px;
        line-height: 40px;
        border-radius: 20px;
        background: #d1ebfa;
        border: 1px solid #2f52ea;
    }
}
</style>





