<template>
  <div class="wrap">
    <div class="title">
      <div class="span-left"><span class="tips"></span>交流中心</div>
      <div class="span-right"><i class="iconfont icon-gengduo"></i></div>
    </div>
    <div class="content">
      <div class="msg">
        <div class="sc_msg">
          <img :src="require('assets/img/exchange_01.jpg')" alt="">
          <span>
            校园圈消息
            <i>13</i>
          </span>
        </div>
        <div class="fr_msg">
          <img :src="require('assets/img/exchange_02.jpg')" alt="">
          <span>
            朋友圈消息
            <i>21</i>
          </span>
        </div>
      </div>
      <div class="msg_more">
        <span>您有31条未读消息</span>
        <i>查看全部</i>
      </div>
      <ul>
        <li v-for="(item, index) in list" :key="index">
          <div class="img">
            <img :src="item.img" alt="">
          </div>
          <div class="msg">
            {{ item.tit }}
            <div class="time">
              {{ item.time }}
            </div>
            <div class="handle">
              <span class="check">
                <i class="el-icon-view"></i> 查看
              </span>
              <span class="del">
                <i class="el-icon-delete"></i> 删除
              </span>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  name: 'exchangeCenter',
  data() {
    return {
      list: [
        {
          img: require('assets/img/head_01.jpg'),
          tit: '张晓华：昨天天气不错哦',
          time: '1分钟前'
        },
        {
          img: require('assets/img/head_02.jpg'),
          tit: '小明：请注意明天不要迟到',
          time: '13：50'
        },
        {
          img: require('assets/img/head_03.jpg'),
          tit: '小李：注意明天下午不要迟到',
          time: '1分钟前'
        },
        {
          img: require('assets/img/head_04.jpg'),
          tit: '小白：新闻新闻',
          time: '13：30'
        },
      ]
    }
  },
  methods: {}
}
</script>
<style lang="scss" scoped>
.wrap{
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  .content {
    box-sizing: border-box;
    padding: 20px 0;
    .msg{
      height: 80px;
      overflow: hidden;
      // vertical-align: middle;
      .sc_msg{
        width: 50%;
        float: left;
        overflow: hidden;
        height: 100%;
        img{
          float: left;
          display: block;
          margin-top: 6px;
        }
        span{
          float: left;
          display: block;
          width: calc(100% - 120px);
          height: 32px;
          border: 1px solid #ffa55d;
          border-radius: 16px 16px;
          color: #ffa55d;
          margin-top: 20px;
          margin-left: 20px;
          line-height: 32px;
          background: #fff5eb;
          text-indent: 20px;
          i{
            display: block;
            width: 20px;
            height: 20px;
            background: #fb0f1d;
            text-align: center;
            color: #fff;
            border-radius: 100%;
            font-size: 12px;
            line-height: 20px;
            text-indent: 0;
            float: right;
            margin-right: 10px;
            margin-top: 5px;
          }
        }
      }
      .fr_msg{
        width: 50%;
        float: left;
        img{
          float: left;
          display: block;
          margin-top: 6px;
        }
        span{
          float: left;
          display: block;
          width: calc(100% - 120px);
          height: 32px;
          border: 1px solid #97c3ff;
          border-radius: 16px 16px;
          color: #97c3ff;
          margin-top: 20px;
          margin-left: 20px;
          line-height: 32px;
          background: #eef4fd;
          text-indent: 20px;
          i{
            display: block;
            width: 20px;
            height: 20px;
            background: #fb0f1d;
            text-align: center;
            color: #fff;
            border-radius: 100%;
            font-size: 12px;
            line-height: 20px;
            text-indent: 0;
            float: right;
            margin-right: 10px;
            margin-top: 5px;
          }
        }
      }
    }
    .msg_more{
      color: #848484;
      line-height: 40px;
      border-bottom: 1px dashed #ccc;
      i{
        cursor: pointer;
        color: #197aff;
        float: right;
        text-decoration: underline;
      }
    }
    ul{
      color: #303030;
      li{
        height: 72px;
        line-height: 72px;
        overflow: hidden;
        border-bottom: 1px dashed #ccc;
        cursor: pointer;
        &:hover{
          img{
            transition: all 1s linear;
            transform: rotateY(360deg);
          }
          .msg{
            color: #197aff;
          }
          .msg .handle{
            display: block;
          }
          .msg .time{
            display: none;
          }
        }

        .img{
          float: left;
          width: 45px;
          height: 100%;
          margin-top: 10px;
        }
        .msg{
          float: left;
          height: 100%;
          width: calc( 100% - 45px);
          .time{
            float: right;
            color: #848484;
          }
          .handle{
            display: none;
            float: right;
            color: #848484;
            .check{
              margin-right: 20px;
              cursor: pointer;
              i{
                color: #197aff;
              }
            }
            .del{
              cursor: pointer;
              i{
                color: #fe9200;
              }
            }
          }
        }
      }
    }
  }
}
</style>



