import Vue from 'vue'
import Router from 'vue-router'
import login from 'pages/login/login'
import index from 'pages/index/index'
import Home from 'pages/home/home'
import page1 from 'pages/page1/page1'
import page2 from 'pages/page2/page2'
import page3 from 'pages/page3/page3'

import cleared from 'pages/cleared/cleared'
import applying from 'pages/cleared/applying'
import approval from 'pages/cleared/approval'

import dealWith from 'pages/officeHall/dealWith'
import examineApprove from 'pages/officeHall/examineApprove'
import fillForm from 'pages/officeHall/fillForm'
import moreServe from 'pages/officeHall/moreServe'
import myHome from 'pages/officeHall/myHome'
import officeIndex from 'pages/officeHall/officeIndex'
import submiting from 'pages/officeHall/submiting'
import successSubmit from 'pages/officeHall/successSubmit'
import officeHallindex from 'pages/officeHall/index.vue'
import unDevelopment from 'pages/unDevelopment/unDevelopment.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',  //路由默认跳转
      redirect: 'login'
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/officeHall',
      name: 'officeHall',
      component: officeHallindex,
      children: [
        {
          path: '/officeHall/dealWith',
          name: 'dealWith',
          component: dealWith
        },
        {
          path: '/officeHall/examineApprove',
          name: 'examineApprove',
          component: examineApprove
        },
        // {
        //   path: '/officeHall/fillForm',
        //   name: 'fillForm',
        //   component: fillForm
        // },
        {
          path: '/officeHall/moreServe',
          name: 'moreServe',
          component: moreServe
        },
        {
          path: '/officeHall/myHome',
          name: 'myHome',
          component: myHome
        },
        {
          path: '/officeHall/officeIndex',
          name: 'officeIndex',
          component: officeIndex
        },
        {
          path: '/officeHall/submiting',
          name: 'submiting',
          component: submiting
        },
        {
          path: '/officeHall/successSubmit',
          name: 'successSubmit',
          component: successSubmit
        }
      ]
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      children: [
        {
          path: '/index/home',
          name: 'home',
          component: Home
        },
        {
          path: '/officeHall/fillForm',
          name: 'fillForm',
          component: fillForm
        },
        {
          path: '/index/messageCenter',
          name: 'messageCenter',
          component: unDevelopment
        },
        {
          path: '/index/serveCenter',
          name: 'serveCenter',
          component: unDevelopment
        },
        {
          path: '/index/dataCenter',
          name: 'dataCenter',
          component: unDevelopment
        },
        {
          path: '/index/decisionSupport',
          name: 'decisionSupport',
          component: unDevelopment
        },
        {
          path: '/index/studentManage',
          name: 'studentManage',
          component: unDevelopment
        },
        {
          path: '/index/graduateManage',
          name: 'graduateManage',
          component: unDevelopment
        },
        {
          path: '/index/identityManage',
          name: 'identityManage',
          component: unDevelopment
        },
        {
          path: '/index/bonusManage',
          name: 'bonusManage',
          component: unDevelopment
        },
        {
          path: '/index/aptitudeManage',
          name: 'aptitudeManage',
          component: unDevelopment
        },
        {
          path: '/index/progectManage',
          name: 'progectManage',
          component: unDevelopment
        },
        {
          path: '/index/cleared',
          name: 'cleared',
          component: cleared
        },
        {
          path: '/index/applying',
          name: 'applying',
          component: applying
        },
        {
          path: '/index/approval',
          name: 'approval',
          component: approval
        },
        {
          path: '/unDevelopment/unDevelopment',
          name: 'unDevelopment',
          component: unDevelopment
        }
      ]
    }

  ],
  linkActiveClass: 'active'
})



