<template>
    <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

    </div>
</template>

<script>
export default {
  name: 'bar',
  props: {
    echartsData: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {}
  },
  watch: {
    echartsData: {
      handler: function(val, oldVal) {
        this.initChart()
      },
      deep: true //增加deep 观察对象的子对象变化
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.myChart = this.$echarts.init(this.$refs.chart)
      this.initChart()
      
    })
  },
  methods: {
    initChart() {
      let option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            lineStyle: {
              color: '#ccc'
            }
          },
          backgroundColor: 'rgba(255,255,255,0.9)',
          textStyle: {
            color: '#666'
          }
        },
        grid: {
          left: '2%',
          right: '4%',
          bottom: '4%',
          top: '4%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            nameTextStyle: {
              color: '#666'
            },
            axisTick: {
              alignWithLabel: true,
              show: false
            },
            axisLabel: {
              textStyle: {
                color: '#666',
                align: 'center'
              },
              margin: 20
            },
            axisLine: {
              lineStyle: {
                color: '#c7defc'
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: '#c7defc'
              }
            },
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
          }
        ],
        yAxis: [
          {
            type: 'value',
            interval: 20,
            axisTick: {
              alignWithLabel: true,
              show: false
            },
            axisLabel: {
              formatter: function(value) {
                var d = value
                return d
              },
              textStyle: {
                color: '#666'
              },
              margin: 20
            },
            axisLine: {
              lineStyle: {
                color: '#c7defc'
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: '#c7defc'
              }
            }
          }
        ],
        color: ['#fb795b', '#02bd2c', '#6ba9fc'],
        series: [
          {
            name: '教育',
            type: 'line',
            symbolSize: 8,
            itemStyle: {
              normal: {
                borderColor: '#fea723',
                borderWidth: 2
              }
            },
            smooth: true,
            lineStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#fb795b'
                  },
                  {
                    offset: 1,
                    color: '#fb795b'
                  }
                ])
              }
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#f9b696'
                  },
                  {
                    offset: 1,
                    color: '#fff'
                  }
                ]),
                opacity: 0.6
              }
            },
            data: [40, 60, 50, 70, 90, 80, 60]
          },
          {
            name: '成绩',
            type: 'line',
            symbolSize: 8,
            itemStyle: {
              normal: {
                borderColor: '#02bd2c',
                borderWidth: 2
              }
            },
            smooth: true,
            lineStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#02bd2c'
                  },
                  {
                    offset: 1,
                    color: '#02bd2c'
                  }
                ])
              }
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#9cd0a7'
                  },
                  {
                    offset: 1,
                    color: '#fff'
                  }
                ]),
                opacity: 0.6
              }
            },
            data: [30, 50, 60, 50, 70, 80, 70]
          },
          {
            name: '升学',
            type: 'line',
            symbolSize: 8,
            itemStyle: {
              normal: {
                borderColor: '#6ba9fc',
                borderWidth: 2
              }
            },
            smooth: true,
            lineStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#6ba9fc'
                  },
                  {
                    offset: 1,
                    color: '#6ba9fc'
                  }
                ])
              }
            },
            areaStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#9bc5fd'
                  },
                  {
                    offset: 1,
                    color: '#fff'
                  }
                ]),
                opacity: 0.6
              }
            },
            data: [40, 20, 50, 40, 50, 40, 80]
          }
        ]
      }
      this.myChart.on('click', params => {
        this.$emit('handleCharts', params.name)
      })
      this.myChart.setOption(option)
      let that = this;
      window.onresize = function(){
        that.myChart.resize()
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



