<template>
  <div class="right-wrap">
    <h2 class="public-title">校园卡办理申请表</h2>
    <div class="table-title">申请人所在单位/部门</div>
    <div class="table-wrap">
      <table>
        <tr>
          <th>单位/部门</th>
          <td class="w40"><input type="text" value="学校研发部"></td>
          <th>负责人</th>
          <td class="w40"><input type="text" value="赵建东"></td>
        </tr>
        <tr>
          <th>经办人</th>
          <td><input type="text" value="张三"></td>
          <th>电话</th>
          <td><input type="text" value="13333333333"></td>
        </tr>
      </table>
    </div>
    <div class="table-title">申请校园卡信息</div>
    <div class="table-wrap">
      <table>
        <tr>
          <th>人员类别</th>
          <td class="w40 pd0">
            <select name="" id="" class="td-select">
              <option value="">教工博士后离退休</option>
              <option value="">教师退休</option>
              <option value="">教工硕士</option>
            </select>
          </td>
          <th>总人数</th>
          <td class="w40"><input type="text" value=""></td>
        </tr>
        <tr>
          <th>申请范围</th>
          <td><input type="text" value="李四"></td>
          <th>拟申请时效</th>
          <td><input type="text" value="13333333333"></td>
        </tr>
      </table>
    </div>
    <div class="table-title">申请事由</div>
    <div class="table-wrap">
      <table>
        <tr>
          <td colspan="2"><textarea name="" id="" cols="30" rows="5" class="area-msg"></textarea></td>
        </tr>
        <tr>

          <th>卡片类型</th>
          <td class="card-type">
            <template>
            <el-radio v-model="radio" label="1">正式</el-radio>
            <el-radio v-model="radio" label="2">临时</el-radio>
          </template>
          </td>
        </tr>
      </table>
    </div>
    <div class="table-title">相关单位、部门意见</div>
    <div class="table-wrap">
      <table>
        <tr>
          <th>相关单位、 部门审核意见</th>
          <td colspan="2"><textarea name="" id="" cols="30" rows="5" class="area-msg"></textarea></td>
        </tr>
      </table>
    </div>
    <div class="table-title">学校办公室意见</div>
    <div class="table-wrap">
      <table>
        <tr>
          <th>申请时效</th>
          <td class="w40"><input type="text" value="学校研发部"></td>
          <th>开通项目</th>
          <td class="w40 pd0">
            <select name="" id="" class="td-select">
              <option value="">餐饮</option>
              <option value="">超市</option>
              <option value="">体育室</option>
            </select>
          </td>
        </tr>
        <tr>
          <th>学校办公室初审意见</th>
          <td colspan="3"><textarea name="" id="" cols="30" rows="5" class="area-msg"></textarea></td>
        </tr>
        <tr>
          <th>学校办公室审意见</th>
          <td colspan="3"><textarea name="" id="" cols="30" rows="5" class="area-msg"></textarea></td>
        </tr>
      </table>
    </div>
    <div class="table-title">网络信息意见</div>
    <div class="table-wrap">
      <table>
        <tr>
          <th>网络信息中心审核意见</th>
          <td colspan="2"><textarea name="" id="" cols="30" rows="2" class="area-msg"></textarea></td>
        </tr>
        <tr>
          <th>办理校园卡人员信息表</th>
          <td colspan="2"><textarea name="" id="" cols="30" rows="1" class="area-msg"></textarea></td>
        </tr>
      </table>
    </div>
    <div class="submit-wrap">
      <span class="submit-btn" @click="submit">提交</span>
      <span class="submit-btn reset-btn" @click="$router.back(-1)">取消</span>
    </div>
  </div>

</template>

<script type="text/javascript">

  export default {
    name: 'fillForm',
    data () {
      return {
        radio: '1'
      };
    },
    mounted(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.setAttribute('class', 'item active')
    },

    destroyed(){
      let dom = document.querySelector('.leftbar-list>.item:nth-child(2)');
      dom.classList.remove("active")
    },
    methods: {
      submit(){
        this.$router.push('/officeHall/successSubmit')
      }
    }
  }
</script>

<style lang="scss" scoped>

  .pd0{
    padding:0!important;
  }
  .right-wrap {
    margin-left: 0.24rem;
    border: 1px solid #d5d5d5;
    display: flex;
    flex-direction: column;
    flex: 1;
    background: #fff;
    border-radius: 0.05rem;
    padding: 0.2rem;
    box-sizing: border-box;
    box-shadow: 0px 5px 24px 0px rgba(0, 0, 0, 0.1);
    .public-title {
      color: #333333;
      font-weight: bold;
      font-size: 0.18rem;
      height: 0.18rem;
      display: flex;
      align-items: center;
      &:before {
        content: '';
        width: 0.05rem;
        margin-right: 0.1rem;
        height: 100%;
        display: inline-block;
        background: -webkit-linear-gradient(to top, #0099cc, #3366ff);
        background: linear-gradient(to top, #0099cc, #3366ff);
      }
    }
    .table-title {
      color: #848484;
      font-size: 0.16rem;
      height: 0.66rem;
      line-height: 0.66rem;
      padding-left: 0.1rem;
      box-sizing: border-box;
    }

    //表格处理部分
    .table-wrap{
      width: 100%;
      color:#666666;
      table{
        width:100%;
        -webkit-border-radius: 0.05rem;
        -moz-border-radius: 0.05rem;
        border-radius: 0.05rem;
        overflow: hidden;
        tr th {
          width:10%;
          font-size: 14px;
          text-align: center;
          background: #f2f1f1;
          border: 1px solid #e2e2e2;
          padding: 0.1rem 0.18rem;
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          box-sizing: border-box;
        }
        tr td.w40{
          width:40%;
        }
        tr td.w40{
          width:40%;
        }
        tr td.w90{
          width:90%;
        }
        .area-msg{
          width:100%;
          border:none;
          padding:0.1rem;
          box-sizing: border-box;
        }
        .card-type{
          text-align: left;
          padding-left:0.3rem;
          box-sizing: border-box;
        }
        .td-select{
          width:100%;
          height:0.42rem;
          display: block;
          padding:0.1rem;
          border:none;
          cursor: pointer;
          background: #fcfcfc;
        }
        input{
          width:100%;
          height:100%;
          padding:0.1rem;
          box-sizing: border-box;
          border:none;
        }
      };

    }
    /*.table-wrap tr:hover {
      background: #f2f1f1;
    }*/

    //表格里的状态样式
    .table-wrap tr td {
      border: 1px solid #e2e2e2;
      text-align: center;
    }
    .submit-wrap{
      height:1rem;
      display: flex;
      flex-direction:row;
      align-items: center;
      justify-content: center;
      .submit-btn{
        display: flex;
        width:1rem;
        height:0.28rem;
        font-size:0.16rem;
        cursor: pointer;
        margin-top:0.34rem;
        line-height:0.28rem;
        border:1px solid #2780e8;
        border-radius: 0.16rem;
        justify-content: center;
        color: #2b73e9;
        background: -moz-linear-gradient(left, rgba(204,255,255,1) 0%, rgba(204,204,255,1) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(left, rgba(204,255,255,1) 0%, rgba(204,204,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(left, rgba(204,255,255,1) 0%, rgba(204,204,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      }
      .reset-btn{
        border:1px solid #7f8896;
        margin-left:0.36rem;
        background: #f3f3f3;
        color: #848484;
      }
    }

  }
</style>



